.home {

    .homeContent {
      background: linear-gradient(115deg, $color-1 50%, transparent 100%);
      min-height: $minHeight;
      width: 100%;
      min-height: 500px;
      border-radius: $borderRadius;
      position: relative;
      overflow: hidden;
      
  
      &:after {
        content: '';
        position: absolute;
        height: 100%;
        width: inherit;
        background: url('../../media/bigGates.jpg') no-repeat center/cover;
        z-index: -1;
        filter: brightness(30%);
      }
  
      .content {
        position: absolute;
        top: 20%;
        left: 15%;
        font-family: $font-1;
  
        // h1 {
        //   font-size: 3rem;
        //   font-family: $font-3;
        //   color: $color-4;
        //   text-transform: uppercase;
        //   // text-shadow: 3px 6px 2px $black;
        // }
        h3 {
          width: 750px;
          overflow-wrap: anywhere;
          font-size: 1.5rem;
          padding: 1.5rem 0.9rem;
          margin-bottom: 1rem;
          color: $color-2;
          // text-shadow: 1px 4px 1px $black;
        }
        .pdf {
          position: absolute;
          // top: 80%;
          // left: 70%;
          a {
            font-size: 1.5rem;
            background: $color-4;
            padding: 1rem 2rem;
            border-radius: $borderRadius;
            box-shadow: 0 5px 1px rgb(15, 15, 15);
            transition: 0.2s ease;
  
            &:hover {
              background: $color-3;
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 850px){
    .homeContent {
      margin: 0 !important;
    }
  }
  @media screen and (max-width:580px){
    .homeContent {
      &:after {
      background: url('../../media/bigGates.jpg') no-repeat 60% 50%/cover !important;
      }
      h3 {
        max-width:250px;
        font-size: .8rem !important;
      }
      .pdf {
        a {
          padding: 0.5rem 1rem !important;
          font-size: .8rem !important;
        }
      }
    }
  }